//react
import React from "react";

//react router dom libary
import { useNavigate ,NavigateFunction} from "react-router-dom";

//axios libary
import axios from "axios";

//cookies libary
import { useCookies } from 'react-cookie';

//alert box
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//page / component
import Input from "../input/input";

//api data interface
interface ApiData {
  email:string;
  password:string;
}

//function
function Signin():JSX.Element{

  //jwt cookies
  const [, setCookiejwt , ] = useCookies(['jwt']);

  //profile pic cookeis
  const [, setCookiepic , ] = useCookies(['pic']);

  //navigation
  const navigate:NavigateFunction = useNavigate()

  //forms submit function 
  function submitform(event : React.FormEvent<HTMLFormElement>){
    
    //stop reload
    event.preventDefault();

    //get data from forms
    const formData = new FormData(event.currentTarget);

    //save data from forms
    const data :ApiData = {
      email:formData.get("email") as string,
      password:formData.get("password") as string,
    }

    //send data to backend
    axios.post(
      process.env.REACT_APP_SIGNIN_URL as string,
      {data},
      {headers:{'Authorization': process.env.REACT_APP_OAUTH_KEY,'Content-Type': 'application/json'}}
    )
      .then(function(res){
        if(res.data.message === "User Found"){
          //seting cookies
          setCookiejwt('jwt', res.data.token, { maxAge: 3600 }); //1 hour
          setCookiepic('pic', res.data.profileurl, { maxAge: 3600 }); //1 hour
          navigate('/');
        }
        else{
          //alert error
          toast.error(res.data.errors);
        }
      })
      .catch(function(err){
        //alert error
        if(Array.isArray(err.response.data.errors)){
          toast.error(err.response.data.errors[0].message);
        }
        else{
          toast.error(err.response.data.errors);
        }
      })
  }

  return(
    <div className="main_div">
      <div className="container">
        <form onSubmit={submitform}>
          <div className="form_div">
            <Input type="email" name="email" label="Email" length={50}/>
            <Input type="password" name="password" label="Password" length={50}/>
            <button type="submit" className="submit_btn">Submit</button>
          </div>
        </form>
      </div>
      <ToastContainer />
    </div>      
  )
}

export default Signin;
