//bootstrap
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

//react router dom libary
import {Link,useNavigate,NavigateFunction} from "react-router-dom";

//cookies libary
import { useCookies } from 'react-cookie';

//css
import "./headerstyle.css"

//function
function Header(): JSX.Element{

  //cookies
  const [cookiesjwt,  , removeCookiejwt] = useCookies(['jwt']);
  const [cookiespic,  , removeCookiepic] = useCookies(['pic']);

  //navigation
  const navigate : NavigateFunction= useNavigate()

  //logout button
  function handleLogout():void{
    removeCookiejwt('jwt');
    removeCookiepic("pic");
    navigate('/signin')
  }
  
  return(
    <div className="header_div">
      <Navbar variant="light" expand="lg">
        <Container>
          <Navbar.Brand className="py-0"><Link to="/" className="header_brand">Blogger</Link></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            {
              cookiesjwt.jwt?
                <Nav className="ms-auto">
                  <button className="header_link header_signout" onClick={handleLogout}>Logout</button>
                  <Link to="/create" className="header_link">Create</Link>
                  <Link to="profile"><img className="header_profile_pic" alt="" src={cookiespic.pic}/></Link>
                </Nav>
                :
                <Nav className="ms-auto">
                  <Link to="/" className="header_link">Home</Link>
                  <Link to="/signin" className="header_link">Sign in</Link>
                  <Link to="/signup" className="header_link">Sign up</Link>
                </Nav>
            }
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
}

export default Header;
