//react
import React from "react";

//axios libary
import axios from "axios";

//alert box
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//page / component
import Input from "../input/input";

//api data interface
interface ApiData {
  name:string;
  email:string;
  password:string;
  profileurl:string;
}

//function
function Signup(): JSX.Element{

  //forms submit function 
  function submitform(event : React.FormEvent<HTMLFormElement>):void{

    //stop reload
    event.preventDefault();

    //get data from forms
    const formData : FormData = new FormData(event.currentTarget);

    //save data from forms
    const data : ApiData= {
      name:formData.get("name") as string,
      email:formData.get("email") as string,
      password:formData.get("password") as string,
      profileurl:formData.get("profileurl") as string,
    }

    //send data to backend
    axios.post(
      process.env.REACT_APP_SIGNUP_URL as string,
      {data},
      {headers:{'Authorization': process.env.REACT_APP_OAUTH_KEY ,'Content-Type': 'application/json'}}
    )
      .then(function(res){
        if(res.data.message === "User account created successfully"){
          //alert message
          toast.success(res.data.message);
        }
        else{
          //alert error
          toast.error(res.data.errors);
        }
      })
      .catch(function(err){
        //alert error
        if(Array.isArray(err.response.data.errors)){
          toast.error(err.response.data.errors[0].message);
        }
        else{
          toast.error(err.response.data.errors);
        }
      })

  }

  return(
    <div className="main_div">
      <div className="container">
        <form onSubmit={submitform}>
          <div className="form_div">
            <Input type="text" name="name" label="Name" length={50}/>
            <Input type="email" name="email" label="Email" length={50}/>
            <Input type="password" name="password" label="Password" length={50}/>
            <Input type="text" name="profileurl" label="Profile Url" length={500}/>
            <button type="submit" className="submit_btn">Submit</button>
          </div>
        </form>
      </div>
      <ToastContainer />
    </div>      
  )
}

export default Signup;
