//react
import { useEffect } from "react";

//react router dom libary
import { useNavigate ,NavigateFunction } from "react-router-dom";

//axios libary
import axios from "axios";

//cookies
import { useCookies } from 'react-cookie';

//alert box
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//page / component
import Input from "../input/input";

//api data interface
interface ApiData {
  subject:string;
  content:string;
  pictureurl:string;
  token:string;
}

//function
function Create(): JSX.Element{

  //cookies
  const [cookiesjwt,  , ] = useCookies(['jwt']);

  //navigation
  const navigate : NavigateFunction= useNavigate()

  //private route
  useEffect(() => {
    if (!cookiesjwt.jwt) {
      navigate('/signin')
    }
  },[cookiesjwt.jwt,navigate]);

  //forms submit function 
  function submitform(event : React.FormEvent<HTMLFormElement>):void{

    //stop reload
    event.preventDefault();

    //get data from forms
    const formData : FormData = new FormData(event.currentTarget);
    
    //save data from forms
    const data : ApiData = {
      subject:formData.get("subject") as string,
      content:formData.get("content") as string,
      pictureurl:formData.get("pictureurl") as string,
      token:cookiesjwt.jwt as string,
    }
    
    //send data to backend
    axios.post(
      process.env.REACT_APP_CREATE_URL as string,
      {data},
      {headers:{'Authorization': process.env.REACT_APP_OAUTH_KEY,'Content-Type': 'application/json'}}
    )
      .then(function(res){
        if(res.data.message === "New Blog Created successfully"){
          //alert message
          toast.success(res.data.message);
        }
        else{
          //alert error
          toast.error(res.data.errors);
        }
      })
      .catch(function(error){
        //alert error
        if(Array.isArray(error.response.data.errors)){
          toast.error(error.response.data.errors[0].message);
        }
        else{
          toast.error(error.response.data.errors);
        }
      })
  }

    return(
      <div className="main_div">
        <div className="container">
          <form onSubmit={submitform}>
            <div className="form_div">
              <Input type="text" name="subject" label="Subject" length={150}/>
              <Input type="text" name="content" label="Content" length={1000}/>
              <Input type="text" name="pictureurl" label="Picture Url" length={500}/>
              <button type="submit" className="submit_btn">Submit</button>
            </div>
          </form>
        </div>
        <ToastContainer />
      </div>
    )
}

export default Create;