//css
import "./inputstyle.css";

//props interface
interface InputProps {
    type: string;
    name: string;
    label: string;
    length: number;
}

//function
function Input({ type, name, label, length}: InputProps):JSX.Element{
    return(
            <div className="col-lg-6 col-md-9 col-sm-12 input_div">
                <div className="floating-label">      
                    <input className="floating-input" type={type} name={name} placeholder=" " maxLength={length}/>
                    <label>{label}</label>
                </div>
            </div>
    )
}
export default Input;