//axios libary
import axios from "axios";

//react
import { useEffect, useState } from "react";

//page / components
import Recent from "./recent/recent";
import Other from "./other/other";
import Loading from "../loading/loading";


//api data interface
interface Apidata {
  _id: string;
  name: string;
  subject: string;
  content: string;
  pictureurl: string;
  __v: number;
}

//function
function Home(): JSX.Element{

  //data state
  const [data, setData] = useState<Apidata[]>([]);

  //is loading state
  const [isLoading, setIsLoading] = useState<boolean>(true);

  //is data loaded state
  const [isDataLoaded, setIsDataLoaded] = useState<boolean>(false);

  //useeffect to fetch data
  useEffect(() => {
    axios.get(
      process.env.REACT_APP_GET_URL as string,
      {headers: { Authorization: process.env.REACT_APP_OAUTH_KEY, "Content-Type": "application/json" }}
    )
      .then(function (res) {
        setData(res.data.data);
        setIsDataLoaded(true);
      })
      .catch(function (error) {
        alert(error.response.data.errors);
      })
      .finally(() => {
        setIsLoading(false);
      });
  },[]);

  if (isLoading) {
    return (<Loading />);
  }

  else if(!isDataLoaded) {
    return (<Loading />);
  }
  
  else{
    return (
      <div className="main_div">
        <div className="container">
          <Recent data={data[data.length - 1]} pos={data.length - 1} />
          <div className="row" style={{ margin: "0px" }}>
            {data
              .filter((_, index) => index !== data.length - 1)
              .map((da, index) => (
                <Other data={da} pos={index} key={index} />
              ))}
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
