//react 
import React, { useEffect, useState } from "react";

//react router dom libary
import { useNavigate ,NavigateFunction} from "react-router-dom";

//cookies libary
import { useCookies } from 'react-cookie';

//axios
import axios from "axios";

//alert box
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//css
import "./profilestyle.css";

//bin icon
import bin from "./dustbin.png";

//api data interface
interface Apidata {
  _id: string;
  name: string;
  subject: string;
  content: string;
  pictureurl: string;
  __v: number;
}

//function
function Profile():JSX.Element{

  //navigation
  const navigate :NavigateFunction= useNavigate();

  //cookies
  const [cookiesjwt,  , ] = useCookies(['jwt']);

  //data state
  const [data, setdata] = useState<Apidata[]>([]);

  //useeffect 
  useEffect(() => {
    //private route
    if (!cookiesjwt.jwt) {
      navigate("/signin");
    }
    else {
      //send data to backend
      axios.post(
        process.env.REACT_APP_PROFILE_URL as string,
        {data:{token:cookiesjwt.jwt}},
        {headers:{"Authorization":process.env.REACT_APP_OAUTH_KEY,"Content-Type": "application/json",},}
      )
        .then(function (res) {
          if (res.data.message === "All Blog Found") {
            //set data
            setdata(res.data.blogs);
          } else {
            //alert error
            toast.error("Unable To Find Blogs");
          }
        })
        .catch(function (error) {
          //alert error
          if (Array.isArray(error.response.data.errors)) {
            toast.error(error.response.data.errors[0].message);
          } else {
            toast.error(error.response.data.errors);
          }
        });
    }
  }, [cookiesjwt.jwt,navigate]);

  //del button function
  function delButton(id: string): React.MouseEventHandler<HTMLButtonElement> {
    return function handleClick(event: React.MouseEvent<HTMLButtonElement>) {

    //send data to backend
      axios.post(
        process.env.REACT_APP_DEL_URL as string,
        {data:{id:id}},
        {headers:{'Authorization': process.env.REACT_APP_OAUTH_KEY,'Content-Type': 'application/json'}}
      )
        .then(function(res){
          if(res.data.message === "Blog Deleted Successfully"){
            //alert success
            toast.success(res.data.message);
          }
          else{
            //alert error
            toast.error(res.data.errors);
          }
        })
        .catch(function(err){
          //alert error
          if(Array.isArray(err.response.data.errors)){
            toast.error(err.response.data.errors[0].message);
          }
          else{
            toast.error(err.response.data.errors);
          }
        })
    }
  }

  //function
  return (
    <div className="main_div">
      <div className="container">
      <ul className="responsive-table">
            <li className="table-header">
                <div className="col col-1">#</div>
                <div className="col col-2">Subject</div>
                <div className="col col-3">Delete</div>
            </li>
            {data.map(function(da,index){
              return(
                <li key={index} className="table-row">
                  <div className="col col-1">{index+1}</div>
                  <div style={{overflow:"hidden"}} className="col col-2">{da.subject}</div>
                  <div className="col col-3">
                    <button className="profile_del" onClick={delButton(da._id)}>
                      <img alt="Delete Button" className="profile_del_img" src={bin}/>
                      </button>
                    </div>
                </li>
              )
            })}
        </ul>
      </div>
      <ToastContainer />
    </div>
  );
}

export default Profile;
